import React from 'react';
// import BackToParent from '../components/BackToParent';
import ProjectSingle from '../components/ProjectSingle';

function Banners(props) {

    const project = {
        category: "Web development",
        year: "2017 - 2020",
        title: "Banners",
        scopeItems: ["Development", "Animation design & development", "Interaction design"],
        summary: "A small screen-capture video collection of web banners I've made at TBWA Helsinki for various different customers. Made with html5 / css / javascript / gsap with custom style & animation implementations.",
        url: "",
        disclaimer: ""
    }

    const playVideo = () => {
        let videoInit = document.querySelectorAll('.video-initializer')[0];
        let videoElem = document.querySelectorAll('.video-element')[0];

        videoInit.style.display = "none";
        videoElem.style.display = "block";
        setTimeout( ()=> { videoElem.play(); }, 200) 
    }
    
    return (
        <div className="project-single">

            {/* Recurring project content (category, title, summary etc.) */}
            <ProjectSingle 
                category={project.category}
                year={project.year}
                title={project.title}
                scopeItems={project.scopeItems}
                summary={project.summary}
                url={project.url}
                disclaimer={project.disclaimer}
            />


            {/* Project-specific content */}
            <div className="project-single-content">
                <button className="video-initializer" onClick={playVideo}>
                    <p>PLAY VIDEO <img className="play-icon" src={require('../assets/icons/playbtn-wht.svg').default} alt=">" /></p>
                    <img src={require('../assets/images/w-banners-blur.jpg').default} alt="Banners big" />
                </button>
                <video style={{ display: "none" }} className="video-element" controls muted controlsList="nodownload" disablePictureInPicture>
                    <source src={require('../assets/images/w-banners-video.mp4').default} type="video/mp4" />
                </video>
            </div>
        </div>
    )
}

export default Banners;