import React, { useRef, useEffect } from 'react';
import './App.scss';
import {BrowserRouter as Router, Switch, Route, NavLink} from 'react-router-dom';
import ScrollToTop from './components/ScrollToTop';
import Frontpage from './page-frontpage';
import Work from './page-work';
import About from './page-about';
import { gsap } from "gsap";
import Footer from './components/Footer';
import { ScrollTrigger } from 'gsap/ScrollTrigger';
gsap.registerPlugin(ScrollTrigger);

// if (typeof window !== `undefined`) {
//     gsap.registerPlugin(ScrollTrigger)
//     gsap.core.globals("ScrollTrigger", ScrollTrigger)
// }


function App() {

    const headerRef = useRef(null);

    useEffect(() => { 

        const parallaxElemTo = (elem, xValue, yValue, triggerElem, startValue) => {
            gsap.to(elem, {
                x: xValue,
                y: yValue,
                ease: 'none',
                scrollTrigger: {
                    scrub: true,
                    // markers: true,
                    trigger: triggerElem,
                    start: startValue,
                }
            });
        };

        // const parallaxElemFrom = (elem, xValue, yValue, triggerElem, startValue) => {
        //     gsap.from(elem, {
        //         x: xValue,
        //         y: yValue,
        //         ease: 'none',
        //         scrollTrigger: {
        //             scrub: true,
        //             markers: true,
        //             trigger: triggerElem,
        //             start: startValue,
        //             end: "bottom top"
        //         }
        //     });
        // };

        parallaxElemTo("#app-header", "0", "-25%", "#app-header", "top top");
        parallaxElemTo("#site-title", "-10vw", "80", "#app-header", "top top");
        parallaxElemTo(".navbar", "10vw", "80", "#app-header", "top top");

    }, []);

    return (
        <div className="App">
            <Router basename="/allu1991.github.io">
            <ScrollToTop />

            <header id="app-header">
                <div className="container" id="header-container">
                    <div id="site-title">
                        <NavLink to="/" className="nav-link">
                            {/* <h1 ref={headerRef}>ALEKSI PITKÄNEN<br /><span>PORTFOLIO</span></h1> */}
                            <h1 ref={headerRef}>
                                <span className="screen-reader-text">Aleksi Pitkänen</span>
                                <img id="logo-aleksi" src={require("./assets/icons/logo-aleksi.svg").default} alt="Aleksi" />
                                <img id="logo-pitkanen" src={require("./assets/icons/logo-pitkanen.svg").default} alt="Pitkänen" />
                                <span>PORTFOLIO</span>
                            </h1>
                        </NavLink>
                    </div>

                    <nav className="navbar">
                        <ul className="nav">
                            <li><NavLink to="/work" className="nav-link">Work</NavLink></li>
                            <li><NavLink to="/about" className="nav-NavLink">About</NavLink></li>
                            {/* <NavLink classes="nav-link" 		href="#info" name="Information" />
                            <NavLink classes="nav-link active" 	href="#development" name="Development" />
                            <NavLink classes="nav-link" 		href="#other" name="Other works" /> */}
                        </ul>
                    </nav>
                </div>
            </header>

            <div className="container" id="main-content">
                <Switch>
                    {/* Set up the Router */}
                    <Route exact path="/">
                        <Frontpage />
                    </Route>
                    <Route path="/work">
                        <Work />
                    </Route>
                    <Route path="/about">
                        <About />
                    </Route>
                    <Route>
                        <h2>Page not found</h2>
                    </Route>
                </Switch>

            </div> {/* content */}

            <Footer />

        </Router>
        </div> // APP
    );
}

export default App;
