import React, {useEffect} from 'react';
import {Link} from 'react-router-dom';
import { gsap } from "gsap";

function Frontpage(props) {

    useEffect(() => {
        // Set the page title
        document.title = "Portfolio | Aleksi Pitkänen";

        // Load SVG file
        document.getElementById("screen-animation").addEventListener("load", function() {

            // Assing SVG file to variable
            const svgFile = this.getSVGDocument();

            // Get sub-elements' paths inside svg file
            const 
                svgHtml = svgFile.querySelectorAll("#svg-html > path"),
                svgCss = svgFile.querySelectorAll("#svg-css > path"),
                svgJs = svgFile.querySelectorAll("#svg-javascript > path"),
                svgWp = svgFile.querySelectorAll("#svg-wordpress > path"),
                svgScss = svgFile.querySelectorAll("#svg-scss > path"),
                svgGsap = svgFile.querySelectorAll("#svg-gsap > path"),
                svgUiux = svgFile.querySelectorAll("#svg-ui-ux > path");
                //console.log(svgHtml);


            // Initialize GSAP timeline for animation
            const svgAnim = gsap.timeline(
                { 
                    delay: 0.6,
                    repeat: 5, 
                    repeatDelay: 1,
                    defaults: {
                        duration: 1,
                        stagger: 0.05,
                        ease: "elastic",
                        transformOrigin: "50% 50%"
                    }
                }
            );

            // GSAP timeline
            svgAnim
                // HTML
                .from(svgHtml, {autoAlpha: 0.0, scale: 0.0})
                .to(svgHtml, {autoAlpha: 0, scale: 0.0, duration: 0.8, ease: "back.out(1.7)"})

                // CSS
                .from(svgCss, {autoAlpha: 0, x: -20})
                .to(svgCss, {autoAlpha: 0, x: 40, duration: 0.8, ease: "back.out(1.7)", 
                    stagger: { each: 0.07, from: "end" } })

                // JAVASCRIPT
                .from(svgJs, {autoAlpha: 0, y: 25})
                .to(svgJs, {autoAlpha: 0, y: 20, duration: 0.8, ease: "back.out(1.7)"})

                // WORDPRESS
                .from(svgWp, {autoAlpha: 0, rotation: 145, duration: 0.5, ease: "back.out(1.7)"})
                .to(svgWp, {autoAlpha: 0, rotation: -145, duration: 0.8, ease: "back.out(1.7)"}, "=+0.4")

                // SCSS
                .from(svgScss, {autoAlpha: 0, y: -20})
                .to(svgScss, {autoAlpha: 0, y: 35, duration: 0.8, ease: "back.out(1.7)"})

                // GSAP
                .from(svgGsap, {autoAlpha: 0, scaleY: 0.0})
                .to(svgGsap, {autoAlpha: 0, scaleX: 0.0, duration: 0.8, ease: "back.out(1.7)"})

                // UI & UX
                .from(svgUiux, {autoAlpha: 0, scale: 1.5, stagger: { each: 0.05, from: "center" }})
                .to(svgUiux, {autoAlpha: 0, scale: 1.3, duration: 0.8, ease: "back.out(1.7)", 
                    stagger: { each: 0.07, from: "center" } });

        });

    }, []);

    return (
        <section id="section-front-page-intro">
            <div className="front-page-intro-content">
                <h2>Greetings!</h2>
                <p>I'm <span>Aleksi Pitkänen</span>, a Finland-based front end developer specializing in digital services and friendly user experiences. If you came here looking for my development works – you're in the right place <img className="thumbs-up" src={require('./assets/icons/thumbs-up.svg').default} alt="Thumbs up" /></p>
                <p>Click the link below to view my collection of various dev projects.</p>
                <Link className="next-page-link btn-shine" to='/work'>VIEW MY WORKS</Link>
            </div>
            <div className="front-page-intro-image">
                <div id="screen-wrapper">
                    <div id="screen">
                        <object type="image/svg+xml" data={require("./assets/images/front-page-animation.svg").default} aria-label="SVG animation" id="screen-animation"></object>

                        <div id="screen-controls">
                            <button id="screen-controls-play" title="Play animation"><img className="play-icon" src={require('./assets/icons/playbtn-wht.svg').default} alt=">" /></button>
                            <label htmlFor="timeline">Timeline</label>
                            <input type="range" id="screen-controls-slider" name="timeline" min="0" max="100" />
                        </div>
                    </div>
                </div>
                <img id="screen-stand" src={require('./assets/images/front-page-intro-img-stand.svg').default} alt="Screen stand" />
            </div>
        </section>
    )
}

export default Frontpage;